import { Component, OnInit } from '@angular/core';
import { MessageService, ConfirmationService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { MobileAddComponent } from '../mobile-add/mobile-add.component';
import { Mobile, MobileGrouped, Version } from '../models/mobile';
import { NgDropDownOption } from '../models/ng-dropdown-option.model';
import { Product } from '../models/product.model';
import { TableColumn } from '../models/table.columns.model';
import { MobileApiService } from '../services/mobile-api.service';
import { VersionAddComponent } from '../version-add/version-add.component';

@Component({
  selector: 'app-mobile-list',
  templateUrl: './mobile-list.component.html',
  styleUrls: ['./mobile-list.component.css'],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class MobileListComponent implements OnInit {
  cols: TableColumn[] = []
  ramOptions: NgDropDownOption[] = []
  colorOptions: NgDropDownOption[] = []
  deviceBrandOptions: NgDropDownOption[] = []
  connectivityOptions: NgDropDownOption[] = []
  storageOptions: NgDropDownOption[] = []
  mobiles: Mobile[] = []
  selectedMobile: Product
  constructor(
    private mobileApiSrvc: MobileApiService,
    public dialogService: DialogService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
  ) { }

  ngOnInit(): void {
    this.getAllMobileData()
    // this.getTableColumns()
  }

  getAllMobileData() {
    this.mobileApiSrvc.getAllMobilePhones().subscribe((res: MobileGrouped) => {
      this.mobiles = res.data;
    }
    )
  }

  mapStringToObject(value: string) {
    return JSON.parse(value);
  }

  // getTableColumns() {
  //   // let columns: string[] = ["Name", "Description","Image", "Price", "Brand", 'Ram', 'connectivity', 'storage', 'color']
  //   let columns: string[] = ["Name", "Description", "Image", "Price", "Brand", 'Ram', 'simCardSlot', 'connectivity']
  //   columns.forEach(column => this.cols.push(new TableColumn(column, column)))
  // }

  getRam() {
    let rams: number[] = []
    this.mobileApiSrvc.getRamOptions().subscribe(data => {
      rams = data;
      rams.forEach(ram => this.ramOptions.push(new NgDropDownOption(ram['id'], ram['ram'])))
    })
  }

  getConnectivity() {
    // let connectivities: string[] = ['2G', '3G', "4G", "5G"]
    let connectivities: string[] = []
    this.mobileApiSrvc.getConnectivityOptions().subscribe(data => {
      connectivities = data;
      connectivities.forEach(connectivity => this.connectivityOptions.push(new NgDropDownOption(connectivity['id'], connectivity['connectivity'])))
    })
  }


  getDeviceBrand() {
    // let deviceBrands: string[] = ['Apple', "Samsung", "Huwawei", "Oppo", "LG", "Motorolla"]
    let deviceBrands: string[] = []
    this.mobileApiSrvc.getDeviceBrand().subscribe(data => {
      deviceBrands = data;
      deviceBrands.forEach(deviceBrand => this.deviceBrandOptions.push(new NgDropDownOption(deviceBrand['id'], deviceBrand['name'])))
    })
  }


  getStorage() {
    // let storages: number[] = [64, 128, 256, 512]
    let storages: number[] = []
    this.mobileApiSrvc.getStorageOptions().subscribe(data => {
      storages = data
      storages.forEach(storage => this.storageOptions.push(new NgDropDownOption(storage['id'], storage['storage'])))
    })
  }


  getColorOptions() {
    // let colors = ['Blue', "Red", "Black", "White", "Grey"]
    let colors = []
    this.mobileApiSrvc.getColorOptions().subscribe(data => {
      colors = data;
      colors.forEach(color => this.colorOptions.push(new NgDropDownOption(color['id'], color['color'])))
    })
  }


  openNew(mode: string, data?) {
    const ref = this.dialogService.open(MobileAddComponent, {
      data: {
        data,
        mode: mode
      },
      header: `${mode} Mobile`,
      width: '50%',
      contentStyle: { "max-height": "500px", "overflow": "auto" }
      // autoZIndex: true
    });

    ref.onClose.subscribe(data => {

      if (data) {
        this.messageService.add({ severity: 'Success', summary: 'Mobile Action', detail: data, life: 3000 });
        this.getAllMobileData();
      }
    })
  }



  deleteMobile(mobile: Mobile) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete ' + mobile.name + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.mobiles = this.mobiles.filter(mob => mobile.id !== mob.id);
        this.mobileApiSrvc.deleteMobile(mobile.id).subscribe(data => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'Mobile Deleted', life: 3000 });
        })
      }
    });
  }

  manageVersion(mode, mobilePhoneId?, data?) {
    const ref = this.dialogService.open(VersionAddComponent, {
      data: {
        data,
        mode,
        mobilePhoneId
      },
      header: `${mode} Version`,
      width: '50%',
      contentStyle: { "max-height": "500px", "overflow": "auto" },
    });

    ref.onClose.subscribe(data => {
      if (data) {
        this.getAllMobileData()
        this.messageService.add({ severity: 'Success', summary: 'Version Action', detail: data, life: 3000 });
      }
    })
  }



  deleteVersion(version: Version) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete this item ?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        // this.mobiles = this.mobiles.filter(mob => mobile.id !== mob.id);
        this.mobileApiSrvc.deleteversion(version.id).subscribe(data => {
          this.getAllMobileData();
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'Version Deleted', life: 3000 });
        })
      }
    });
  }



}
