import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { MessageService } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { NgDropDownOption } from '../models/ng-dropdown-option.model';
import { MobileApiService } from '../services/mobile-api.service'

@Component({
  selector: 'app-mobile-add',
  templateUrl: './mobile-add.component.html',
  styleUrls: ['./mobile-add.component.css'],
  providers: [MessageService]

})
export class MobileAddComponent implements OnInit {
  mode: string;
  data: {}
  ramOptions: NgDropDownOption[] = [];
  deviceBrandOptions: NgDropDownOption[] = [];
  connectivityOptions: NgDropDownOption[] = [];
  simCardSlotOptions: NgDropDownOption[] = [];
  storageOptions: NgDropDownOption[] = [];
  colorsOptions: NgDropDownOption[] = [];
  highlightOptions: NgDropDownOption[] = [];
  hotDealOptions: NgDropDownOption[] = []
  selectedBrand: string;
  selectedRam: string;
  selectedConnectivity: string;
  mobileForm: FormGroup;
  constructor(
    private messageService: MessageService,
    public ref: DynamicDialogRef,
    public config: DynamicDialogConfig,
    private MobileApiService: MobileApiService
  ) { }

  async ngOnInit() {
    this.mode = this.config.data.mode
    this.createMobileForm();
    this.inAddMode();
    await this.getDeviceBrand();
    await this.getRam();
    await this.getConnectivity();
    await this.getsimCardSlotOptions();
    this.getStorage();
    this.getColor();
    this.getHighlight();
    this.gethotDeal();
    this.data = this.config.data.data ? this.config.data.data : null
    let versions: [] = this.data ? this.data['versions'] : []
    if (this.data) {
      this.mobileForm.patchValue(this.data)
      // this.mobileForm.patchValue({
      //   name: this.data['name'],
      //   description: this.data['description'],
      //   productImage: this.data['productImage'],
      //   price: this.data['price'],
      //   deviceBrandId: this.deviceBrandOptions.filter(brands => brands.label === this.data['deviceBrand'])[0]?.value,
      //   ramId: this.ramOptions.filter(rams => rams.label === this.data['ram'])[0]?.value,
      //   simCardSlotId: this.simCardSlotOptions.filter(sim => sim.label === this.data['simCardSlot'])[0]?.value,
      //   connectivityId: this.connectivityOptions.filter(con => con.label === this.data['connectivity'])[0]?.value,
      //   highlight: this.data['highlight'],
      //   hotDeal: this.data['hotDeal']
      // })
    }
    else {
      this.mobileForm.patchValue({
        highlight: false,
        hotDeal: false
      })
    }
  }

  createMobileForm() {
    this.mobileForm = new FormGroup({
      name: new FormControl('', [Validators.required]),
      description: new FormControl('', [Validators.required]),
      mobileBrandId: new FormControl('', [Validators.required]),
      mobileRamId: new FormControl('', [Validators.required]),
      mobileSimCardId: new FormControl('', [Validators.required]),
      mobileConnectivityId: new FormControl('', [Validators.required]),
    })
  }

  inAddMode() {
    if (this.mode === "Add") {
      this.mobileForm.addControl("videoUrl", new FormControl('', []))
      this.mobileForm.addControl("versions", new FormArray([this.createVersion(this.data)], [Validators.required]))
    }
  }




  async getDeviceBrand() {
    let deviceBrands: string[] = []
    await this.MobileApiService.getDeviceBrand().toPromise()
      .then(data => {
        deviceBrands = data;
        deviceBrands.forEach((deviceBrand, index) => this.deviceBrandOptions.push({ label: deviceBrand['name'], value: deviceBrand['id'] }))
      })
  }


  async getRam() {
    let rams: number[] = []
    await this.MobileApiService.getRamOptions().toPromise()
      .then(data => {
        rams = data;
        rams.forEach(ram => this.ramOptions.push({ label: ram['name'], value: ram['id'] }))
      })
  }

  async getConnectivity() {
    let connectivities: string[] = []
    await this.MobileApiService.getConnectivityOptions().toPromise()
      .then(data => {
        connectivities = data;
        connectivities.forEach(connectivity => this.connectivityOptions.push({ label: connectivity['name'], value: connectivity['id'] }))
      })
  }

  async getsimCardSlotOptions() {
    let simCardSlots: string[] = [];
    await this.MobileApiService.getSimCardOptions().toPromise()
      .then(data => {
        simCardSlots = data;
        simCardSlots.forEach(simCardSlot => this.simCardSlotOptions.push({ label: simCardSlot['name'], value: simCardSlot['id'] }))
      })
  }


  getStorage() {
    let storages: number[] = []
    this.MobileApiService.getStorageOptions().subscribe(data => {
      storages = data;
      storages.forEach(storage => this.storageOptions.push({ label: storage['name'], value: storage['id'] }))
    })
  }

  getColor() {
    let colors: string[] = [];
    this.MobileApiService.getColorOptions().subscribe(data => {
      colors = data;
      colors.forEach(color => this.colorsOptions.push({ label: color['name'], value: color['id'] }))
    })
  }

  getHighlight() {
    this.highlightOptions.push({ label: 'True', value: true })
    this.highlightOptions.push({ label: 'False', value: false })
  }

  gethotDeal() {
    this.hotDealOptions.push({ label: 'True', value: true })
    this.hotDealOptions.push({ label: 'False', value: false })
  }

  ImageBaseData: any

  selectImage(event) {
    let reader = new FileReader();
    reader.readAsDataURL(event.currentFiles[0]);
    reader.onload = () => {
      this.ImageBaseData = reader.result;
      this.ImageBaseData = this.ImageBaseData.split(',')[1];
      this.Versions.patchValue([{
        ProductImage: this.ImageBaseData
      }])
    };

    reader.onerror = function (error) {
      console.log('Error: ', error);
    };


  }

  get Versions() {
    const items = this.mobileForm.get('vserions') as FormArray;
    return this.mobileForm.get('versions') as FormArray;
  };

  createVersion(data?): FormGroup {
    return new FormGroup({
      MobileStorageId: new FormControl(data ? data.mobileStorageName : '', []),
      MobileColorId: new FormControl(data ? data.mobileColorName : '', []),
      Price: new FormControl(data ? data.price : '', [Validators.required]),
      DiscountPrice: new FormControl(data ? data.discountPrice : '', []),
      ProductImage: new FormControl(data ? data.productImage : '', []),
      Quantity: new FormControl(data ? data.quantity : '', []),
      Highlight: new FormControl(data ? data.highlight : '', []),
      HotDeal: new FormControl(data ? data.hotDeal : '', []),
    });
  }

  addNewVesion() {
    this.Versions.push(this.createVersion());
  }

  deleteVersion(i) {
    this.Versions.removeAt(i);
  }

  handleMobile() {
    if (!this.mobileForm.valid) {
      this.mobileForm.markAllAsTouched();
      this.messageService.add({ severity: 'Error', summary: 'Required Field', detail: 'Required Input', life: 3000 });
      return
    }

    if (this.mode === "Add") {
      this.MobileApiService.addMobile(this.mobileForm.value).subscribe(data => {
        this.ref.close('Mobile Added');
      })
    } else if (this.mode === "Edit") {
      let mobileEdited = this.mobileForm.value;
      mobileEdited['id'] = this.data['id'];
      this.MobileApiService.editMobile(this.data['id'], this.mobileForm.value).subscribe(data => {
        this.ref.close('Mobile Edited');
      })
    }


  }

}
