import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MobilesRoutingModule } from './mobiles-routing.module';
import { MobileListComponent } from './mobile-list/mobile-list.component';
import { MobileAddComponent } from './mobile-add/mobile-add.component';
import { MobileEditComponent } from './mobile-edit/mobile-edit.component';
import { TableModule } from 'primeng/table';
import { DropdownModule } from 'primeng/dropdown';
import { MultiSelectModule } from 'primeng/multiselect';
import { ButtonModule } from 'primeng/button';
import { ToolbarModule } from 'primeng/toolbar';
import { FileUploadModule } from 'primeng/fileupload';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DynamicDialogModule } from 'primeng/dynamicdialog';
import { ContextMenuModule } from 'primeng/contextmenu';
import { ToastModule } from 'primeng/toast';
import { InputTextModule } from 'primeng/inputtext';
import { InputNumberModule } from 'primeng/inputnumber';
import { ConfirmDialogModule } from 'primeng/confirmdialog';
import { VersionAddComponent } from './version-add/version-add.component';



@NgModule({
  declarations: [MobileListComponent, MobileAddComponent, MobileEditComponent, VersionAddComponent],
  imports: [
    CommonModule,
    MobilesRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    TableModule,
    DropdownModule,
    MultiSelectModule,
    ButtonModule,
    ToolbarModule,
    FileUploadModule,
    DynamicDialogModule,
    MultiSelectModule,
    ContextMenuModule,
    ToastModule,
    InputTextModule,
    InputNumberModule,
    ConfirmDialogModule
  ],
  entryComponents: [MobileAddComponent, VersionAddComponent]
})
export class MobilesModule { }
