import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { Version } from '../models/mobile';
import { NgDropDownOption } from '../models/ng-dropdown-option.model';
import { MobileApiService } from '../services/mobile-api.service'

@Component({
  selector: 'app-version-add',
  templateUrl: './version-add.component.html',
  styleUrls: ['./version-add.component.scss']
})
export class VersionAddComponent implements OnInit {
  versionForm: FormGroup;
  mode: string;
  data: Version
  mobilePhoneId
  storageOptions: NgDropDownOption[] = [];
  colorsOptions: NgDropDownOption[] = [];
  highlightOptions: NgDropDownOption[] = [];
  hotDealOptions: NgDropDownOption[] = []
  constructor(
    private messageService: MessageService,
    public ref: DynamicDialogRef,
    public config: DynamicDialogConfig,
    private MobileApiService: MobileApiService
  ) { }

  async ngOnInit() {
    this.createVersionForm();
    await this.getStorage();
    await this.getColor();
    this.getHighlight();
    this.gethotDeal()

    this.mode = this.config.data.mode;
    this.mobilePhoneId = this.config.data.mobilePhoneId;
    this.data = this.config.data.data;

    if (this.mode !== "Add" && this.data) {
      this.versionForm.patchValue({
        MobileStorageId: this.data.mobileStorageId,
        MobileColorId: this.data.mobileColorId,
        Price: this.data.price,
        DiscountPrice: this.data.discountPrice,
        ProductImage: this.data.productImage,
        Quantity: this.data.quantity,
        Highlight: this.data.highlight,
        HotDeal: this.data.hotDeal,
      });
    }
  }


  createVersionForm() {
    this.versionForm = new FormGroup({
      MobileStorageId: new FormControl('', []),
      MobileColorId: new FormControl('', []),
      Price: new FormControl('', [Validators.required]),
      DiscountPrice: new FormControl('', []),
      ProductImage: new FormControl('', []),
      Quantity: new FormControl('', []),
      Highlight: new FormControl('', []),
      HotDeal: new FormControl('', []),
    })
  }

  async getStorage() {
    let storages: number[] = []
    await this.MobileApiService.getStorageOptions().toPromise()
      .then(data => {
        storages = data;
        storages.forEach(storage => this.storageOptions.push({ label: storage['name'], value: storage['id'] }))
      })
  }

  async getColor() {
    let colors: string[] = [];
    await this.MobileApiService.getColorOptions().toPromise()
      .then(data => {
        colors = data;
        colors.forEach(color => this.colorsOptions.push({ label: color['name'], value: color['id'] }))
      })
  }

  getHighlight() {
    this.highlightOptions.push({ label: 'True', value: true })
    this.highlightOptions.push({ label: 'False', value: false })
  }

  gethotDeal() {
    this.hotDealOptions.push({ label: 'True', value: true })
    this.hotDealOptions.push({ label: 'False', value: false })
  }

  ImageBaseData: any

  selectImage(event) {
    let reader = new FileReader();
    reader.readAsDataURL(event.currentFiles[0]);
    reader.onload = () => {
      this.ImageBaseData = reader.result;
      this.ImageBaseData = this.ImageBaseData.split(',')[1];
      this.versionForm.patchValue({
        ProductImage: this.ImageBaseData
      })
    };

    reader.onerror = function (error) {
      console.log('Error: ', error);
    };


  }



  handleVersion() {

    if (!this.versionForm.valid) {
      return this.versionForm.markAllAsTouched();
    }

    if (this.mode === "Add") {
      this.addVersion(this.mobilePhoneId, this.versionForm.value);
    }
    else {
      this.editVersion(this.mobilePhoneId, this.versionForm.value);
    }

  }



  addVersion(mobilePhoneId: number, version: Version) {
    version['mobilePhoneId'] = mobilePhoneId
    this.MobileApiService.addVersion(version).subscribe(data => {
      this.ref.close('Version Added')
    })

  }


  editVersion(mobilePhoneId: number, version: Version) {
    version['mobilePhoneId'] = mobilePhoneId
    version['id'] = this.data['id']
    this.MobileApiService.updateVersion(version).subscribe(data => {
      this.ref.close('Version Edited ')
    })
  }

}
